.container{
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(to right,#2af598,lightblue);
}
.wrapper{
  /* border: 2px solid black; */
  width: 70vw;
  height: 70vh;
  margin: 5rem auto;
  overflow: hidden;
  box-shadow: 1px 2px 30px rgba(0, 0, 0, 0.562);
}
.slider-holder{
  display: grid;
  grid-template-columns: repeat(3,100%);
  height: 100%;
  animation-name: slide;
  animation-duration: 15s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}
#slider_image_1{
  background: lawngreen;
}
img{
  width: 100%;
  height: 100%;
}
#slider_image_2{
  background: yellow;

}
#slider_image_3{
  background: tomato;

}
.button-holder{
  position: absolute;
  bottom:0%;
  left: 45%;
}
.button-holder .dots{
  display: inline-block;
  height: 15px;
  width: 15px;
  border-radius: 3rem;
  background-color: lightcyan;
  margin: 4rem .3rem;
  color: lightcyan;
  text-decoration: none;
}
@keyframes slide{
  0%{
      transform: translateX(0%);
      
  }
  10%{
      transform: translateX(-100%);

  }
  20%{
      transform: translateX(-100%);

  }
  30%{
      transform: translateX(-100%);

  }
  40%{
      transform: translateX(-200%);

  }
  50%{
      transform: translateX(-200%);

  }
  60%{
      transform: translateX(-200%);

  }
  70%{
      transform: translateX(0%);

  }
  80%{
      transform: translateX(0%);

  }
  90%{
      transform: translateX(0%);

  }
  100%{
      transform: translateX(0%);

  }
}